import server from 'utils/request';

/**
 *  
 * @param file  excel  
 */
export function deviceBatch (file) {
  var data = {
    file
  }
  return server({
    url: '/device/batch',
    method: 'post',
    data,
    transformRequest: [function (data) {
      var bodyFormData = new FormData()
      for (let it in data) {
        bodyFormData.set(it, data[it])
      }
      return bodyFormData
    }]
  })
}

/**
 *
 * @param data   
 * -------------------------------------------
 * deviceNo         
 * userno           
 * qrcode           
 * name             
 * brand             
 * model             
 * arrival_date      
 * affairs_no       
 * status           
 * create_no         
 * username         
 * dept             
 * office           
 * company           
 * remark           *
 */
export function deviceUpdate(data) {
  data = {
    ...data
  }
  return server({
    url: '/device/update',
    method: 'post',
    data,
    transformRequest: [function (data) {
      var bodyFormData = new FormData()
      for (let it in data) {
        if (it.indexOf('time') === -1) {
          bodyFormData.set(it, data[it])
        } else {
            delete data[it]
        }
      }
      console.log()
      return bodyFormData
    }]
  })
}

